<template>
 <div class="app-container" >
    <router-view></router-view>
    <!--底部导航-->
    <nav v-if='$store.getters.hasShow==0' id='nav' class="mui-bar mui-bar-tab">
        <router-link class="mui-tab-item" :class="$store.getters.hasActive[0]?'mui-active':''" to='/home'>
            <span @click='chgActive(0)' class="mui-icon mui-icon-home"></span>
            <span  @click='chgActive(0)' class="mui-tab-label">首页</span>
        </router-link>
        <router-link class="mui-tab-item" to="/sort"  :class="$store.getters.hasActive[1]?'mui-active':''">
            <span @click='chgActive(1)' class="mui-icon mui-icon-extra mui-icon-extra-class"></span>
            <span  @click='chgActive(1)' class="mui-tab-label">分类</span>
        </router-link>
        <router-link class="mui-tab-item" to="/find" :class="$store.getters.hasActive[2]?'mui-active':''"  >
            <span @click='chgActive(2)' class="mui-icon mui-icon-extra mui-icon-extra-find"></span>
            <span @click='chgActive(2)' class="mui-tab-label">发现</span>
        </router-link>
        <router-link class="mui-tab-item" to="/shop" :class="$store.getters.hasActive[3]?'mui-active':''" >
            <span @click='chgActive(3)' class="mui-icon mui-icon-extra mui-icon-extra-cart">
                <span class="mui-badge" v-cloak>{{$store.getters.optCount}}</span>
            </span>
            <span  @click='chgActive(3)' class="mui-tab-label">购物车</span>
        </router-link>
        <router-link class="mui-tab-item" to="/my" :class="$store.getters.hasActive[4]?'mui-active':''" >
            <span @click='chgActive(4)' class="mui-icon mui-icon-contact"></span>
            <span @click='chgActive(4)' class="mui-tab-label">我的</span>
        </router-link>
    </nav>
    <!--购物车底部-->
    <nav v-else-if='$store.getters.hasShow==1' class="mui-bar mui-bar-tab">
        <router-link class="payleft" to=''>
            <div>
                <span class="mui-icon-extra mui-icon-extra-cart">
                    <span class="mui-badge" v-cloak>{{$store.getters.hasChk}}</span>
                </span>
            </div>
            <p v-cloak>{{'￥'+$store.getters.hasTotal.toFixed(2)}}</p>
            <p>免费包邮</p>
        </router-link>
        <router-link class="payright" to="">
            去结算
        </router-link>
    </nav>
    <!--商品详情底部-->
    <nav v-else-if='$store.getters.hasShow==2' class="mui-bar mui-bar-tab">
        <router-link class="detailleft" to=''>
            加入购物车
        </router-link>
        <router-link class="detailright" to="">
            立即购买
        </router-link>
    </nav>
 </div>
</template>
<script>
    export default {
        data(){
            return{
                shopNum:0
            }
        },
        methods:{
            //改变底部导航栏激活状态
            chgActive(i){
                //console.log(this.$store.getters.hasActive)
                // for(var m=0;m<this.isActive.length;m++){
                //     this.isActive[m]=false
                // }
                // this.isActive[i]=true;
                if(i==3) this.$store.commit('chgShow',1)
                else this.$store.commit('chgShow',0)
                this.$store.commit('change',i)
            },
            getNum(){
                if(this.$route.path=='/shop'){
                    this.$store.commit('chgShow',1)
                    this.$store.commit('change',3)
                }else if(this.$route.path=='/sort' || this.$route.path=='/goods' || this.$route.path=='/goodsInfo'){
                    this.$store.commit('change',1)
                }else if(this.$route.path=='/find'){
                    this.$store.commit('change',2)
                }else if(this.$route.path=='/my' || this.$route.path=='/comment' ){
                    this.$store.commit('change',4)
                }
                this.axios.get('http://127.0.0.1:3008/store/shopcart')
                .then(res=>{
                    this.$store.commit('increment',res.data.length)
                })
            }
        },
        created(){
            this.getNum()
        }
    }
</script>
<style>
    [v-cloak]{display:none;}
    .app-container{
        padding-top:0;
        padding-bottom:0;
        overflow:hidden;
        width:100%;
    }
    .app-container .mui-bar{
        position:fixed;
        bottom:0;
        box-shadow:0 0 0.5px #ccc;
    } 
    nav.mui-bar-tab .mui-tab-item mui-tab-label span{
        color:#333;
    }
    nav.mui-bar-tab .mui-tab-item.mui-active{
        color:#FFB72F;
    }
    .app-shop .mui-bar{
        position:fixed;
        bottom:0;
        box-shadow:0 0 0.5px #ccc;
    }
    .payleft,.payright{
        position:absolute;
        height:100%;
        font-size:1.1rem;
    }
    .payleft{
        left:0;
        width:55%; 
    }
    .payright{
        right:0;
        width:45%;
        color:white;
        background:#FFB72F;
        text-align:center;
        padding-top:1rem;
    }
    .payleft>p{
        position:relative;
        color:black;
    }
    .payleft>div:first-child{
        position:relative;
        top:-0.9rem;
        left:1rem;
        width:3.5rem;
        height:3.5rem;
        border:2px solid #ccc;
        border-radius:50%;
        background:#FFB720;
        text-align:center;
        line-height:3.5rem;
        font-size:1rem;
    }
    .payleft>div:first-child .mui-badge{
        position:absolute;
        top:0;
        right:0;
        background:red;
    }
    .payleft>div:first-child span{
        color:white;
        font-weight:bold;
    }
    .payleft>p:nth-child(2){
        font-weight:bold;
        top:-3rem;
        font-size:1.1rem;
        left:5.5rem;
    }
    .payleft>p:last-child{
        color:#666;
        font-size:0.6em;
        bottom:3.8rem;
        left:5.7rem;
    }
    .detailleft,.detailright{
        position:absolute;
        height:100%;
        font-size:1.1rem;
        width:50%;
        text-align:center;
        padding-top:0.8em;
        color:white;
    }
    .detailleft{left:0;background:#FFB720;}
    .detailright{right:0;background:red;}


    .mui-bar-tab .mui-tab-item-tao.mui-active {
        color: black;
    }
    .mui-bar-tab .mui-tab-item-tao {
        display: table-cell;
        overflow: hidden;
        height: 50px;
        width: 1%;
        text-align: center;
        vertical-align: middle;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: black;
    }   
    .mui-bar-tab .mui-tab-item-tao .mui-icon {
        top: 3px;
        width: 24px;
        height: 24px;
        padding-top: 0;
        padding-bottom: 0;
    }
    .mui-bar-tab .mui-tab-item-tao .mui-icon~.mui-tab-label {
        font-size:11px;
        display:block;
        overflow:hidden;
        text-overflow:ellipsis;
    }
    
</style>



