import Vue from 'vue'
import Router from 'vue-router'
import HelloContainer from "./components/HelloWorld.vue"
import HomeContainer from "./components/home/homeContainer.vue"
import SortContainer from "./components/sort/sortContainer.vue"
import FindContainer from "./components/find/findContainer.vue"
import ShopContainer from "./components/shop/shopContainer.vue"
import MyContainer from "./components/my/myContainer.vue"
import GoodsContainer from "./components/goods/goodsContainer.vue"
import GoodsInfo from "./components/goods/goodsInfo.vue"
import CommentContainer from "./components/my/commentContainer.vue"
Vue.use(Router)

export default new Router({
  routes: [
    {path:'/',redirect:'/home'},
    {path:'/home',component:HomeContainer},
    {path:'/sort',component:SortContainer},
    {path:'/find',component:FindContainer},
    {path:'/shop',component:ShopContainer},
    {path:'/my',component:MyContainer},
    {path:'/goods',component:GoodsContainer},
    {path:'/goodsInfo',component:GoodsInfo},
    {path:'/comment',component:CommentContainer}
  ]
})



