<template>
    <div class='app-my'>
        <header class="mui-bar mui-bar-nav">
		    <a @click='goBack(0)' class="mui-icon mui-icon-home"></a>
            <h1 class="mui-title">我的信息</h1>
            <a class='mui-icon mui-icon-more'></a>
		</header>
        <div class='content'>
            <div class='member'>
                <img src='../../img/body.jpg'>
                <p>嘻哈杂货铺</p> 
                <div class='menu'>
                    <ul>
                        <li>
                            <i class='icon-aui-my icon-qb'></i>
                            <p>钱包</p>
                        </li>
                        <li>
                            <i class='icon-aui-my icon-kq'></i>
                            <p>卡券</p>
                        </li>
                        <li>
                            <i class='icon-aui-my icon-qi'></i>
                            <p>签到</p>
                        </li>
                    </ul>
                </div>
            </div>
            <div class='mymenu'>
                <div>
                    <a href='#'>我的订单</a>
                    <a href='#'>全部订单<span class='mui-icon mui-icon-arrowright'></span></a>
                </div>
                <div>
                    <ul>
                        <li>    
                            <i class='icon-aui-my icon-fk'></i>
                            <p>待付款</p>
                        </li>
                        <li>    
                            <i class='icon-aui-my icon-fh'></i>
                            <p>待发货</p>
                        </li>
                        <li>    
                            <i class='icon-aui-my icon-sh'></i>
                            <p>待收货</p>
                        </li>
                        <li @click='jumpComment()'>    
                            <i class='icon-aui-my icon-pj'></i>
                            <p>待评价</p>
                        </li>
                        <li>    
                            <i class='icon-aui-my icon-th'></i>
                            <p>退货/售后</p>
                        </li>
                    </ul>
                </div>
            </div>
            <ul class="mui-table-view">
                <li class="mui-table-view-cell">
                    <a class="mui-navigate-right">
                        <span class='mui-icon mui-icon-person c1'></span>
                        &nbsp;超级会员
                    </a>
                </li>
                <li class="mui-table-view-cell">
                    <a class="mui-navigate-right">
                        <span class='mui-icon mui-icon-pengyouquan c2'></span>
                        &nbsp;我的推广
                    </a>
                </li>
                <li class="mui-table-view-cell">
                    <a class="mui-navigate-right">
                        <span class='mui-icon-extra mui-icon-extra-express c3'></span>
                        &nbsp;地址管理
                    </a>
                </li>
                <li class="mui-table-view-cell">
                    <a class="mui-navigate-right">
                        <span class='mui-icon mui-icon-chat c4'></span>
                        &nbsp;我的消息
                    </a>
                </li>
                <li class="mui-table-view-cell">
                    <a class="mui-navigate-right">
                        <span class='mui-icon-extra mui-icon-extra-heart c5'></span>
                        &nbsp;我的收藏
                    </a>
                </li>
                <li class="mui-table-view-cell">
                    <a class="mui-navigate-right">
                    <span class='mui-icon mui-icon-compose c6'></span>                   
                        &nbsp;实名认证
                    </a>
                </li>
                <li class="mui-table-view-cell">
                    <a class="mui-navigate-right">
                        <span class='mui-icon-extra mui-icon-extra-gift c7'></span>
                        &nbsp;我的权益
                    </a>
                </li>
                <li class="mui-table-view-cell">
                    <a class="mui-navigate-right">
                        <span class='mui-icon-extra mui-icon-extra-custom c8'></span>
                        &nbsp;我的咨询
                    </a>
                </li>
                <li class="mui-table-view-cell">
                    <a class="mui-navigate-right">
                        <span class='mui-icon mui-icon-locked c9'></span>
                        &nbsp;账号安全
                    </a>
                </li>
            </ul>
            <button type="button" class="mui-btn mui-btn-danger">
                退出账号
            </button>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return{}
        },
        methods:{
            goBack(i){
                this.$store.commit('change',i)
                this.$router.push('/home')
            },
            jumpComment(){
                //this.$store.commit('chgShow',3)
                this.$router.push('/comment')
            }
        }
    }
</script>
<style>
    div.app-my .mui-bar{
        background:#FFB72F;
    }
    div.app-my .mui-bar a{
        color:white;
    }
    div.app-my .mui-bar a:nth-child(3){
        float:right;
        transform:rotate(90deg);
    }
    div.app-my .mui-bar h1{
        color:white;
        letter-spacing:1px;
    }
    div.app-my .content {
        position:relative;
        top:2.9rem;
        width:100%;
        margin-bottom:8rem;
    }
    div.app-my .content .member{
        position:relative;
        width:100%;
        background:white;
        padding-bottom:0.5rem;
    }
    div.app-my .content .member>img{
        position:relative;
        width:22%;
        border-radius:50%;
        left:50%; 
        margin-left:-11%;
        margin-top:1rem;
    }
    div.app-my .content .member>p{
        position:relative;
        width:22%;
        color:#ccc;
        text-shadow:#333 0 1px 0;
        text-align:center;
        bottom:0;
        left:50%; 
        margin-left:-11%;
        letter-spacing:1px;
    }
    div.app-my .content .menu{
        position:relative;
        width:100%;
        height:4rem;
        padding-top:0.5rem;
    }
    div.app-my .content .menu>ul{
        position:relative;
        width:100%;
        list-style:none;
        padding:0;
        margin:0;
    }
    div.app-my .content .menu>ul>li{
        position:relative;
        float:left;
        width:33.333%;
        text-align:center;
    }
    div.app-my .content .menu>ul>li>p{
        font-size:0.8rem;
        color:#333;
    }
    div.app-my .content .mymenu{
        position:relative;
        width:100%;
        background:white;
        margin-top:0.5rem;
    }
    div.app-my .content .mymenu>div:first-child{
        padding:0.6rem 0;
        border-bottom:1px solid #ccc;
    }
    div.app-my .content .mymenu>div>a{      
        color:#333;
        font-size:0.8rem;
    }
    div.app-my .content .mymenu>div>a:first-child{
        margin-left:1rem;
    }
    div.app-my .content .mymenu>div>a:last-child{
        float:right;
        margin-right:1rem;
    }
     div.app-my .content .mymenu>div>a:last-child>span{
         font-size:1rem;
     }
    div.app-my .content .mymenu>div>ul{
        position:relative;
        float:left;
        list-style:none;
        width:100%;
        margin:0;
        padding:0;
        padding-top:0.5rem;
        background:white;
    }
    div.app-my .content .mymenu>div>ul>li{
        float:left;
        width:20%;
        text-align:center;
    }
    div.app-my .content>button{width:100%;}
    div.app-my .content .mymenu>div>ul>li>p{font-size:0.7rem;color:#333;}
    div.app-my .content>ul{margin-top:4.8rem;}
    div.app-my .content>ul>li>a{font-size:0.7em;}
    div.app-my .content>ul>li>a>span{font-size:1.2em;}
    .c1{color:#FFC125;}
    .c2{color:#20B2AA}
    .c3{color:#CD5C5C;}
    .c4{color:#20B2AA}
    .c5{color:#CD5C5C;}
    .c6{color:#00868B}
    .c7{color:#FA8072;}
    .c8{color:#20B2AA}
    .c9{color:#CD5C5C;}
    @media screen and (min-width:769px){
        div.app-my .content .member>p{
            font-size:1rem;
        }
    }

</style>


