import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import Qs from 'qs'
import "mint-ui/lib/style.css"

Vue.config.productionTip = false
Vue.prototype.axios = axios
Vue.prototype.Qs=Qs
//5: 设置请求的根路径 
//Vue.http.options.root = "http://127.0.0.1/vue_ser/";
//6:全局设置post 时候表音的数据组织格式为 application/x-www-form-urlencoded
//Vue.http.options.emulateJSON = true;
// 导入 MUI 的样式表， 和 Bootstrap 用法没有差别
import "./lib/mui/css/mui.css"
//导入aui-css
import "./lib/mui/css/icon.css"
//导入font-awesome字体图标
import "./lib/font-awesome/css/font-awesome.css"
// 导入 MUI 的样式表，扩展图标样式，购物车图标
// 还需要加载图标字体文件
import "./lib/mui/css/icons-extra.css"
//import './lib/mui/js/mui.min.js'
//import {Header,Swipe,SwipeItem} from 'mint-ui'
// Vue.component(Header.name,Header)
// Vue.component(Swipe.name,Swipe)
// Vue.component(SwipeItem.name,SwipeItem)
import MintUI from "mint-ui"
Vue.use(MintUI)
Vue.directive('focus',{
  inserted(elem){
    elem.focus()
  }
})
import Vuex from 'vuex';
Vue.use(Vuex);
var store=new Vuex.Store({
  state:{
    count:0,
    isShow:0,
    chkNum:0,
    total:0,
    loadNum:[0,0,0],
    isActive:[true,false,false,false,false]
  },
  mutations:{
      increment(state,c){
        state.count=c;
      },
      change(state,i){
        for(var m=0;m<state.isActive.length;m++){
            state.isActive[m]=false
        }
        state.isActive[i]=true;
      },
      chgShow(state,c){
        state.isShow=c
      },
      getChkNum(state,c){
        if(c!=0)
          state.chkNum+=c
        else
          state.chkNum=c
      },
      getTotal(state,c){
        if(c!=0)
          state.total+=c
        else
          state.total=c
      },
      chgLoad(state,c){
        state.loadNum[c]=1
      }
  },
  getters:{
    optCount(state){
      return state.count;
    },
    hasActive(state){
      return state.isActive;
    },
    hasShow(state){
      return state.isShow;
    },
    hasChk(state){
      return state.chkNum;
    },
    hasTotal(state){
      return state.total
    },
    hasLoad(state){
      return state.loadNum
    }
  }
})
//日期过滤器
Vue.filter('dateFilter',function(val){
  var d = new Date(val);
  var y = d.getFullYear()
  var m = d.getMonth()+1;
  var day = d.getDate()
  var h = d.getHours()
  var min = d.getMinutes()
  var s = d.getSeconds()
  if(m<10) m='0'+m
  if(day<10) day='0'+day
  if(h<10) h='0'+h
  if(min<10) min='0'+min
  if(s<10) s='0'+s
  return `${y}-${m}-${day} ${h}:${min}:${s}`;
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')



