<script>
    export default {
        data(){
            return {
                imgList:[],
                firstList:[]
            }
        },
        methods:{
            //获取轮播图片
            getBanner(){
                this.axios.get('http://127.0.0.1:3008/store/banner')
                .then(res=>{
                    this.imgList=res.data
                })
            },
            //获取商品楼层的数据
            getFirst(){
                this.axios.get('http://127.0.0.1:3008/store/first')
                .then(res=>{
                    this.firstList=res.data;
                })
            },
            //实现图片左右滑动
            dragBanner(e){
                var x=e.changedTouches[0].pageX;
                var oMeter='0px',meter;
                if(e.target.nodeName=='IMG'){
                    oMeter=getComputedStyle(e.target.parentElement).left;
                }else{
                     oMeter=getComputedStyle(e.target).left;
                }
                document.ontouchmove=function(e){
                    if(e.changedTouches[0].pageX<0){
                        document.ontouchmove=null;
                    }else{
                        meter=eval(e.changedTouches[0].pageX-x+parseFloat(oMeter));
                        if(meter>0){
                            meter=oMeter=0
                        } 
                        else if(meter<-632){
                            meter=oMeter=-632
                        }
                        if(e.target.nodeName=='IMG'){
                            e.target.parentElement.style.left=meter+'px';
                        }else{
                            e.target.style.left=meter+'px';
                        }  
                    }
                       
                     //console.log(e.target)              
                }
                document.ontouchend=function(){
                    document.ontouchmove=null;
                }
            }
        },
        created(){
            this.getBanner()
            this.getFirst()
        }
    }
</script>
