<template>
    <div class='app-shop'>
        <header class="mui-bar mui-bar-nav">
		    <a @click='goBack(0)' class="mui-icon mui-icon-home"></a>
            <h1 class="mui-title">购物车</h1>
            <a class='mui-icon mui-icon-more'></a>
		</header>
        <div class='content'>
            <div class="content-title">
                <div class="mui-input-row mui-checkbox mui-left">
                    <label>锤子旗舰店</label>
                    <input @click='checkAll()' name="checkbox" value="Item 1" type="checkbox" :checked='chkAll'>
                </div>
                <ul>
                    <li><a href='#'>领券</a></li>
                    <li>丨</li>
                    <li><a href='#'>编辑</a> </li>
                </ul>
			</div>
            <div class="mui-card" v-for='(item,i) in shopCarts' :key='item.id'>
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
						<img src='../../img/shop1.png'>
                        <h4 v-html='item.s_title'></h4>
                        <p>
                            <span v-cloak>{{item.s_weight+'kg'}}</span>
                            &nbsp;&nbsp;&nbsp;<span v-cloak>{{'套餐:'+item.s_meal}}</span>
                        </p>
                        <h5 v-cloak>{{'￥'+item.s_price.toFixed(2)}}</h5>
                        <div class="mui-numbox">
                            <button @click='devNum(item.id,i)' class="mui-btn mui-btn-numbox-minus" type="button">－</button>
                            <input class="mui-input-numbox" type="number" :value='item.s_num'/>
                            <button @click='addNum(item.id,i)' class="mui-btn mui-btn-numbox-plus" type="button">+</button>
                        </div>
                        <div class="mui-input-row mui-checkbox mui-left">
						    <label>1</label>
						    <input @click='checkOne(i)' name="checkbox" type="checkbox" :checked='chk[i]'>
					    </div>
                        <span @click='delShop()' class='mui-icon mui-icon-trash'></span>
					</div>
				</div>
			</div>
        </div>
    </div>
</template>
<script>
    import {Toast} from 'mint-ui';
    export default {
        data(){
            return{
                chkAll:false,
                shopCarts:[],
                chk:[],
                total:0,
                fid:0
            }
        },
        methods:{
            goBack(i){
                this.$store.commit('change',i)
                this.$store.commit('chgShow',0)
                this.$router.push('/home')
            },
            devNum(id,i){
                for(var item of this.shopCarts){
                    if(item.id==id){
                        if(item.s_num>1){
                            item.s_num--;
                            if(this.chk[i]){
                                this.$store.commit('getTotal',-(this.shopCarts[i].s_price))
                                this.$store.commit('getChkNum',-1);
                            }
                        }else{
                            Toast({
                                message:'已达到最小购买数量',
                                duration:2000
                            });
                            this.devState=true
                        }    
                    }
                }
            },
            addNum(id,i){
                for(var item of this.shopCarts){
                    if(item.id==id){
                        if(item.s_num<99){
                            item.s_num++;
                            if(this.chk[i]){
                                this.$store.commit('getTotal',this.shopCarts[i].s_price) 
                                this.$store.commit('getChkNum',1);  
                            }                           
                        }else{
                            Toast({
                                message:'已达到最大购买数量',
                                duration:2000
                            });
                            this.addState=true
                        }
                    }
                }
            },
            checkAll(){
                this.chkAll=!this.chkAll
                this.chk.filter((elem,i)=>{
                    this.chk[i]=this.chkAll
                });
                this.getSum(10)
            },
            checkOne(i){
                this.chk[i]=!this.chk[i];
                this.chkAll=this.chk.every(elem=>{
                    return elem==true
                });
                this.getSum(i)
            },
            getShopCart(){
                this.axios.get('http://127.0.0.1:3008/store/shopcart')
                .then(res=>{
                    this.shopCarts=res.data;
                    for(var k=0;k<this.shopCarts.length;k++){
                        this.chk[k]=false;
                    }  
                    this.$store.commit('increment',this.shopCarts.length)
                })
            },
            getSum(i){
                if(this.fid==i && this.total>0){
                    this.$store.commit('getTotal',-(this.shopCarts[i].s_num*this.shopCarts[i].s_price));
                    this.fid=-1;
                    this.$store.commit('getChkNum',-(this.shopCarts[i].s_num));
                }else if(i!=10){
                    if(this.chk[i]){
                        this.$store.commit('getTotal',this.shopCarts[i].s_num*this.shopCarts[i].s_price);
                        this.fid=i;
                        this.$store.commit('getChkNum',this.shopCarts[i].s_num)
                    }else{
                        this.$store.commit('getTotal',-(this.shopCarts[i].s_num*this.shopCarts[i].s_price));
                        this.fid=-1;
                         this.$store.commit('getChkNum',-(this.shopCarts[i].s_num))
                    }
                }else{ 
                    this.$store.commit('getTotal',0);
                    this.$store.commit('getChkNum',0);
                    for(var s of this.shopCarts){
                       this.$store.commit('getTotal',s.s_num*s.s_price);
                         this.$store.commit('getChkNum',s.s_num)
                    }
                }
                var c=this.chk.every(elem=>{
                    return elem==false
                })
                if(c){
                    this.$store.commit('getTotal',0);
                    this.$store.commit('getChkNum',0)
                } 
            },
            delShop(){
                Toast('删除成功')
            }
        },
        created(){
            this.getShopCart()
        }
    }
</script>
<style>
    [v-cloak]{display:none;}
    div.app-shop .mui-bar:first-child{
        background:#FFB72F;
    }
    div.app-shop .mui-bar:first-child a{
        color:white;
    }
    div.app-shop .mui-bar a:nth-child(3){
        float:right;
        transform:rotate(90deg);
    }
    div.app-shop .mui-bar:first-child h1{
        color:white;
        letter-spacing:1px;
    }
    div.app-shop .content{
        position:relative;
        width:100%;
        top:2.9rem;
        margin-bottom:7rem;
    }
    div.app-shop .content:after{
        content:'';
        display:block;
        clear:both;
    }
    div.app-shop .content .content-title{
        position:relative;
        width:100%;
        height:3rem;
        top:0;
        left:0;
        border-top:1px solid #ccc;
        border-bottom:1px solid #ccc;
        line-height:3rem;
        background:white;
        color:#333;
    }
    div.app-shop .content .content-title>div>label{
        width:50%;
        color:red;
        font-size:0.85em;
        padding-left:2.5rem;
        padding-top:-0.5rem;
        letter-spacing:1px;
    }
    div.app-shop .content .content-title>ul{
        position:absolute;
        list-style:none;
        top:-0.1rem;
        right:1%;
        width:9.5rem;
        padding:0;
        margin:0;
        padding-left:5rem;
    }
    div.app-shop .content .content-title>ul>li{float:left;}
    div.app-shop .content .content-title>ul>li>a{
        color:#333;
        font-size:0.8rem;
        padding-bottom:0.1rem;
    }
    div.app-shop .content .content-title>ul>li:nth-child(2){
        padding-top:0.15rem;
        font-size:0.8rem;
        color:#999;
    }
    div.app-shop .content .content-title>div>input{
        position:absolute;
        left:0.3rem;
        margin-top:0.5rem;
    }
    div.app-shop .content .mui-card{
        position:relative;
        top:0.5rem;
        width:100%;
        height:9rem;
        background:white;
        border:1px solid #ccc;
        margin:0 0 0.1rem 0;
    }
    .mui-card .mui-card-content .mui-card-content-inner img{
        position:relative;
        top:-1rem;
        left:1.1rem;
        width:8rem;
        height:9rem;
    }
    .mui-card .mui-card-content .mui-card-content-inner h4{
        position:absolute;
        width:50%;
        height:2.2rem;
        top:1.5rem;
        right:8%;
        font-size:0.8rem;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
        letter-spacing:1px;
        line-height:1rem;
    }
    .mui-card .mui-card-content .mui-card-content-inner p{
        position:absolute;
        width:50%;
        right:8%;
        top:4.3rem;
        font-size:0.8em;
    }
    .mui-card .mui-card-content .mui-card-content-inner h5{
        position:absolute;
        width:50%;
        right:8%;
        top:6rem;
        font-size:1.1rem;
        color:red;
        font-weight:bold;
    }
    .mui-card .mui-card-content .mui-card-content-inner .mui-numbox{
        position:absolute;
        right:3%;
        top:5.6rem;
        height:2rem;
        width:5.4rem;
        padding:0;
    }
    .mui-card .mui-card-content .mui-card-content-inner .mui-numbox>button{
        width:1.8rem;
        height:2rem;   
    }
    .mui-card .mui-card-content .mui-card-content-inner .mui-numbox>button:first-child{
        border-right:1px solid #bbb;
    }
    .mui-card .mui-card-content .mui-card-content-inner .mui-numbox>button:last-child{
        border-left:1px solid #bbb;
    }
    .mui-card .mui-card-content .mui-card-content-inner .mui-numbox>input{
        width:1.8rem;
        height:2rem;
        border-left:1px solid #bbb;
        border-right:1px solid #bbb;
        font-size:16px;
    }
    .mui-card .mui-card-content .mui-card-content-inner .mui-left{
        position:absolute;
        top:3.5rem;
        left:-1rem;
        width:2.9rem;
    }
    
    span.mui-icon.mui-icon-trash{
        position:absolute;
        top:5%;
        right:2%;
        color:#999 !important;
    }
    .mui-checkbox input[type='checkbox']:checked:before{
        color:#FFB72F !important;
    }
    .mui-checkbox input[type='checkbox']:before {
        font-size:1.4rem !important;
    }
</style>


