<template>
    <div class='app-goods'>
        <header class="mui-bar mui-bar-nav">
		    <a @click='goBack()' class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <input type="text" v-focus placeholder="细心挑选 时尚女装">
            <a href='#'>
                <span class=" mui-icon mui-icon-search"></span>
            </a>
            <a @click='showMenu()' class='mui-icon mui-icon-more'></a>
		</header> 
        <transition name='fade'>
                <ul v-if='isMenu'>
                    <li class='gactive'>时尚女装</li>
                    <li>时尚男装</li>
                    <li>休闲运动</li>
                    <li>精品手表</li>
                    <li>母婴专区</li>
                    <li>智能电视</li>
                    <li>精品手表</li>
                    <li>休闲零食</li>
                    <li>五谷杂粮</li>
                    <li>日常用品</li>
                    <li>移动电源</li>
                    <li>个人护理</li>
                    <li>生活家居</li>
                    <li>平板电脑</li>
                </ul>
            </transition>
        <div v-if='isSee' class='load'>
            <i class="fa fa-spinner fa-pulse"></i><br><br>
            <span>加载中</span>
        </div>
        <div v-else class='content'>
            <div class='goodnav'>
                <ul>
                    <li>综合 <i class='fa fa-sort-desc'></i></li>
                    <li>销量 <i class='fa fa-sort'></i></li>
                    <li>价格 <i class='fa fa-sort'></i></li>
                    <li>筛选 <i class='fa fa-filter'></i></li>
                </ul>
                <ul>
                    <li>款式 <i class='fa fa-sort-desc'></i></li>
                    <li>品牌 <i class='fa fa-sort-desc'></i></li>
                    <li>尺码 <i class='fa fa-sort-desc'></i></li>
                    <li>颜色 <i class='fa fa-sort-desc'></i></li>
                </ul>
            </div>
            <div class='goodlist'>
                <div @click='jumpInfo()' class='gooditem' v-for='item in goods' :key='item.id'>
                    <img v-lazy='item.gurl'>
                    <h4 v-text='item.gdesc'></h4>
                    <span v-text='item.gprice'></span>
                    <del v-text='item.goldprice'></del>
                    <span v-cloak>{{item.gpresent+'%好评'}}</span>
                </div>
            </div>
            <button v-if='hasMore' v-text='btnMsg' @click='getGoods()' type="button" class="mui-btn mui-btn-dange">    
            </button>
             <button v-else v-text='btnMsg' type="button"  class="mui-btn mui-btn-defaul" disabled>
            </button>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return{
                isSee:true,
                isMenu:false,
                btnMsg:'加载更多',
                hasMore:true,
                goods:[],
                pno:0,
                pageSize:8
            }
        },
        methods:{
            goBack(){
                this.$store.commit('chgShow',0)
                this.$router.push('/sort')
            },
            getGoods(){
                this.$store.commit('chgShow',3)
                this.pno++;
                this.axios.post(
                    'http://127.0.0.1:3008/store/goods',
                    this.Qs.stringify({
                        id:1,
                        pno:this.pno,
                        pageSize:this.pageSize
                    })    
                )
                .then(res=>{
                    this.goods=this.goods.concat(res.data.list)
                    if(this.pno>=res.data.count){
                        this.btnMsg='没有更多了~'
                        this.hasMore=false;
                    }
                })
            },
            jumpInfo(){
                this.$store.commit('chgShow',2)
                this.$router.push('/goodsInfo')
            },
            showMenu(){
                this.isMenu=!this.isMenu
            },
            delay(){
                var t = setTimeout(()=>{
                    this.$store.commit('chgLoad',0)
                    this.isSee=false;
                },1500)
            }
        },
        created(){
            this.getGoods()
            if(this.$store.getters.hasLoad[0]==0) this.delay() 
            else this.isSee=false;
        }
    }
</script>
<style>
    .fade-enter-active, .fade-leave-active {
        transition: all 0.5s linear;
    }
    .fade-enter, .fade-leave-to {
        opacity:0;
        transform:translateY(-10rem);
    }
    .app-goods>ul{
        position:fixed;
        list-style:none;
        top:2.73rem;
        margin:0;
        padding:0;
        right:0;
        background:white;
        width:100%;
        z-index:1;
    }
    .app-goods>ul:after{
        content:'';
        display:block;
        clear:both;
    }
    .app-goods>ul>li{
        float:left;
        width:25%;
        height:2.5rem;
        text-align:center;
        line-height:2.5rem;
        font-size:0.8rem;
        border-bottom:1px solid #eee;
    }
    .gactive{color:red;}
    [v-cloak]{display:none;}
    div.app-goods header input{
        position:absolute;
        width:76.5%;
        height:2.25rem;
        top:0.26rem;
        left:12%;
        border-radius:20px;
        font-size:0.8rem;
        background:#FFF3E1;
        letter-spacing:1px;
        border:0;
        caret-color:#999;
    }
    div.app-goods header a:nth-child(3)>span{
        position:absolute;
        top:0;
        color:gray;
        right:16%;
        z-index:1000;
    }
    div.app-goods .mui-bar{
        background:#FFB72F;
    }
    div.app-goods .mui-bar a{
        color:white;
    }
    div.app-goods .mui-bar a:first-child{
        position:absolute;
        left:0.5rem;
    }
    div.app-goods .mui-bar a:nth-child(4){
        float:right;
        transform:rotate(90deg);
    }
    div.app-goods .mui-bar h1{
        color:white;
        letter-spacing:1px;
    }   
    div.app-goods .load{
        position:absolute;
        top:16rem;
        width:100%;
        height:10rem;
        text-align:center;
        padding-top:1rem;
    }
    div.app-goods .load>i{
        font-size:3.2rem;
        color:#999;
        margin:0;
    }
    div.app-goods .load>span{
        font-size:0.8rem;
        color:#666;
        letter-spacing:2px;
    }
    div.app-goods .content{
        position:relative;
        top:2.9rem;
        margin-bottom:5rem;
    }
    div.app-goods .content .goodnav{
        position:relative;
        width:100%;
    } 
    div.app-goods .content .goodnav:after{
        content:'';
        display:block;
        clear:both;
    }
    div.app-goods .content .goodnav ul:first-child{
        float:left;
        width:100%;
        list-style:none;
        margin:0;
        padding:0.2rem 0;
        border-bottom:1px solid #ccc;
        background:white;
    }
    div.app-goods .content .goodnav ul:first-child>li{
        float:left;
        width:25%;
        text-align:center;
        font-size:0.8em;
        color:#333;
    }
    div.app-goods .content .goodnav ul>li>i{
        color:#666;
    }
    div.app-goods .content .goodnav ul:first-child>li:first-child {color:red;}
    div.app-goods .content .goodnav ul:first-child>li:first-child i{vertical-align:top;color:red;}
    div.app-goods .content .goodnav ul:last-child{
        float:left;
        width:100%;
        list-style:none;
        margin:0;
        padding:0.3rem 0;
        border-bottom:1px solid #ccc;
        background:white;
    }
    div.app-goods .content .goodnav ul:last-child>li{
        float:left;
        width:23%;
        text-align:center;
        font-size:0.8em;
        color:#333;
        padding:0.2rem 0;
        background:#ddd;
        margin:0 1%;
    }
    div.app-goods .content .goodnav ul:last-child>li i{ vertical-align:top;}
    div.app-goods .content .goodlist{
        position:relative;
        width:100%;
    }
    div.app-goods .content .goodlist:after{
        content:'';
        display:block;
        clear:both;
    }
    div.app-goods .content .goodlist .gooditem{
        float:left;
        width:48%;
        margin:1%;
        height:14rem;
        background:white;
    }
    div.app-goods .content .goodlist .gooditem img{
        position:relative;
        top:0;
        left:0;
        width:100%;
        height:75%;
    }
    div.app-goods .content .goodlist .gooditem h4{
        position:relative;
        top:0.1rem;
        left:3%;
        width:95%;
        color:#333;
        font-size:0.8rem;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
        margin-top:0.25rem;
        padding-top:2px;
    }
    div.app-goods .content .goodlist .gooditem span:nth-child(3){
        position:relative;
        top:0;
        left:2%;
        width:20%;
        font-weight:bold;
        color:red;
    }
    div.app-goods .content .goodlist .gooditem del:nth-child(4){
        position:relative;
        top:0;
        left:3%;
        width:10%;
        font-size:0.8rem;
        color:#999;
    }
    div.app-goods .content .goodlist .gooditem span:nth-child(5){
        float:right;
        width:36%;
        font-size:0.8rem;
        color:#999;
    }
    .mui-btn-dange{
        border:0 !important;
        background-color:#FFB72F !important;
        width:100%;
        color:white !important;
        letter-spacing:1px;
    }
    .mui-btn-defaul{
        width:100%; 
        background-color:#eee !important;
        letter-spacing:1px;
    }
    .goodlist>div>img[lazy=loading]{
        width:100%;
        background:#eee;
    }
</style>



