<template>
    <div class='app-comment'>
        <header class="mui-bar mui-bar-nav">
		    <a @click='goBack()' class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <h1 class="mui-title">商品评价</h1>
            <a class='mui-icon mui-icon-more'></a>
		</header>
        <div v-if='isSee' class='load'>
            <i class="fa fa-spinner fa-pulse"></i><br><br>
            <span>加载中</span>
        </div>
        <div v-else class='content'>
            <div class='com-header'>
                <p>成交时间：2018-11-08 11:00:12</p>
                <h4>评价宝贝</h4>
                <hr>
                <img src='../../img/find1.jpg'>
                <h4>Huawei mate10 pre</h4>
                <p>送礼才是新年正确的打开方式，可是什么样<br>的礼物才能深入人心并且在生活中有实质性的作用呢？香烟美酒特产美食？太过大众化。补品佳肴？价格高实际作用小。那到底什么样的新年礼物才算好呢？都说新年新气象，那不如送出能帮助别人打造“新气象”的礼物，用一套出色的理容设备，让亲朋好友们在新的一年里能有一个全新的姿态。</p>
                <div>
                    <span>评价：</span>
                    <i v-for='i  in 5' @click='getStart($event,i)' :key='i' class='fa fa-star'></i>
                </div>
            </div>
            <div class='comm'>
                <textarea placeholder='请输入对商品的评价...' v-model='tArea'></textarea>
                <button :disabled='isBan' @click='subComment()' type="button" class="mui-btn mui-btn-danger">
                    {{btnMsg}}
                </button>
            </div>
            <div class='comment'>
                <h4>商品评价（999+）</h4>
                <a>查看全部<span class='mui-icon mui-icon-arrowright'></span></a>
                <hr>
            </div>
            <div class="mui-card">
				<div class="mui-card-header">
                    <img src='../../img/body.jpg'>
                    <span>默**yu</span>
                    <span><i class='fa fa-map-marker'></i> 福建省福州市</span>
                    <span v-cloak>{{m}}星{{str}}评：</span>
                    <span><i v-for='i in stars' :key='i' class='fa fa-star'></i></span>
                </div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner" v-text='coms'></div>
				</div>
				<div class="mui-card-footer" v-cloak>{{cTime|dateFilter}}</div>
			</div>
        </div>
    </div>
</template>
<script>
    import {Toast} from 'mint-ui';
    export default {
        data(){
            return{
                isSee:true,
                stars:5,
                n:5,
                m:5,
                str:'好',
                btnMsg:'发表评价',
                isBan:false,
                coms:'商品质量不错，发货速度也很快，物有所值，以后会常来。',
                cTime:'2018-11-09 14:22:16',
                tArea:''
            }
        },
        methods:{
            goBack(){
                //this.$store.commit('chgShow',0)
                this.$router.push('/my')
            },
            getStart(e,i){
                this.n=0;
                var s=e.target.parentElement;
                s=s.querySelectorAll('i') 
                for(var k=0;k<s.length;k++){
                    if(k>=i-1) {
                        s[k].style.color='#FFB72F'
                        this.n++;
                    }
                    else s[k].style.color='#bbb'
                }
            },
            subComment(){
                if(this.tArea.trim()=='') Toast('评价内容不能为空')
                else {
                    this.coms = this.tArea;
                    this.cTime = new Date();
                    this.tArea='';
                    Toast('评价成功')
                    this.stars=this.n
                    this.m=this.n
                    this.isBan=true
                    this.btnMsg='已发表'
                    if(this.m>3) this.str='好'
                    else if(this.m==3) this.str='中'
                    else this.str='差'
                }
            },
            delay(){
                var t = setTimeout(()=>{
                    this.$store.commit('chgLoad',2)
                    this.isSee=false;
                },1000)
            }
        },
        created(){
           if(this.$store.getters.hasLoad[2]==0) this.delay() 
           else this.isSee=false;
        }
    }
</script>
<style>
    [v-cloak]{display:none;}
    div.app-comment .mui-bar{
        background:#FFB72F;
    }
    div.app-comment .mui-bar a{
        color:white;
    }
    div.app-comment .mui-bar a:nth-child(3){
        float:right;
        transform:rotate(90deg);
    }
    div.app-comment .mui-bar h1{
        color:white;
        letter-spacing:1px;
    }
    div.app-comment .load{
        position:absolute;
        top:16rem;
        width:100%;
        height:10rem;
        text-align:center;
        padding-top:1rem;
    }
    div.app-comment .load>i{
        font-size:3.2rem;
        color:#999;
        margin:0;
    }
    div.app-comment .load>span{
        font-size:0.8rem;
        color:#666;
        letter-spacing:2px;
    }
    div.app-comment .content{
        position:relative;
        top:2.9rem;
        margin-bottom:6rem;
    }
    div.app-comment .content:after{
        content:'';
        display:block;
        clear:both;
    }
    div.app-comment .content .com-header{
        position:relative;
        top:0;
        width:100%;
        background:white;
        height:12rem;
    }
    div.app-comment .content .com-header>p:first-child{
        position:absolute;
        top:0.2rem;
        width:60%;
        background:#eee;
        margin:0;
        text-align:center;
        left:50%;
        margin-left:-30%;
        border-radius:10px;
        font-size:0.7rem;
    }
    div.app-comment .content .com-header>h4:nth-child(2){
        position:absolute;
        margin:0;
        top:2.5rem;
        left:3%;
        letter-spacing:3px;
        font-size:1rem;
        font-weight:normal;
    }
    div.app-comment .content .com-header>hr{
        position:absolute;
        top:4rem;
        width:100%;
        height:1px;
        background-color:#eee;
        border:0;
    }
    div.app-comment .content .com-header>img{
        position:absolute;
        width:6rem;
        height:6rem;
        top:5.5rem;
        left:3%;
    }
    div.app-comment .content .com-header>h4:nth-child(5){
        position:absolute;
        top:5.2rem;
        right:3%;
        width:66%;
        color:#333;
        font-weight:bold;
        font-size:1rem;
    }
    div.app-comment .content .com-header>p:nth-child(6){
        position:absolute;
        top:7rem;
        right:3%;
        width:66%;
        color:#999;
        font-size:0.7rem;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
    }
    div.app-comment .content .com-header>div:nth-child(7){
        position:absolute;
        top:10rem;
        right:3%;
    }
    div.app-comment .content .com-header>div>span{
        position:relative;
        top:-0.2rem;
        left:0.2rem;
        font-size:0.8rem;
        color:#333;
        letter-spacing:3px;
    }
    div.app-comment .content .com-header>div>i{
        float:right;
        margin-right:0.3rem;
        color:#bbb;
        margin-left:0.5rem;
    }
    div.app-comment .content div.comm{
        position:relative;
        left:0;
        top:0;
        width:100%;
        background:white;
        padding-bottom:1rem;
    }
    div.app-comment .content div.comm>textarea{
        position:relative;
        width:92%;
        left:4%;
        height:5rem;
        margin:0 0 0.5rem 0;
        font-size:0.75rem;
        color:#666;
        background:#eee;
    }
    div.app-comment .content div.comm .mui-btn-danger{
        width:100%;
        background:#FFB72F !important;
        border:0;
    }
    div.app-comment .content .comment{
        position:relative;
        top:0.5rem;
        width:100%;
        background:white;
    }
    div.app-comment .content .comment>hr{
        position:absolute;
        bottom:0;
        width:100%;
        height:1px;
        background:#eee;
        border:0;
        margin:0;
    }
    div.app-comment .content .comment{top:1rem;height:2.3rem;}
    div.app-comment .content .comment h4{
        position:absolute;
        top:0.5rem;
        left:2%;
        font-weight:normal;
        font-size:0.8rem;
        color:#666;
        
    }
    div.app-comment .content .comment a{
        position:absolute;
        top:0.5rem;
        right:2%;
        font-size:0.7rem;
        color:red;
    }
    div.app-comment .content .comment a>span{
        font-size:1rem;
    }
    div.app-comment .mui-card{
        margin:1.1rem 0;
        width:100%;
    }
    div.app-comment .mui-card .mui-card-content-inner{color:#333;letter-spacing:1px;font-size:0.8rem;}
    div.app-comment .mui-card .mui-card-header>img{border-radius:50%;}
    div.app-comment .mui-card .mui-card-header>span:not(:last-child){
        position:absolute;
        font-size:0.8rem;
        color:#666;
    }
    div.app-comment .mui-card .mui-card-header>span:nth-child(2){
        top:0.5rem;
        left:3.5rem;
    }
    div.app-comment .mui-card .mui-card-header>span:nth-child(3){
        top:1.6rem;
        left:3.5rem;
        font-size:0.7rem;
    }
    div.app-comment .mui-card .mui-card-header>span:nth-child(3)>i{
        font-size:0.8rem;
        color:#FFB72F;
    }
    div.app-comment .mui-card .mui-card-header>span:nth-child(4){
        top:1.1rem;
        right:4.5rem;
    }
    div.app-comment .mui-card .mui-card-header>span:last-child{
        position:absolute;
        right:0.7rem;
        width:17%;
        text-align:left;
    }
    div.app-comment .mui-card .mui-card-header>span:last-child>i{
        font-size:0.8rem;
        color:#FFB72F;
    }
    div.app-comment .mui-card .mui-card-footer{
        font-size:0.8rem;
        color:#999;
        justify-content:flex-end;    
    }
</style>


