<template>
    <div class='app-sort'>
        <header class="mui-bar mui-bar-nav">
		    <a @click='goBack(0)' class="mui-icon mui-icon-home"></a>
            <h1 class="mui-title">商品分类</h1>
            <a class='mui-icon mui-icon-more'></a>
		</header>
        <img src='../../img/title.png'>
        <img src='../../img/head2.jpg'>
        <div class='content'>
            <ul class="mui-table-view mui-grid-view mui-grid-9">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4" v-for='(s,i) in sortList' :key='s.id'>
                    <a href="#" @click.prevent='jump(s.id)'>
                        <img v-lazy='s.imgurl'>
                        <div class="mui-media-body" v-text='s.tit'></div>
                    </a>
                </li>           
            </ul> 
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return{
                sortList:[]
            }
        },
        methods:{
            goBack(i){
                this.$store.commit('change',i)
                this.$router.push('/home')
            },
            getImg(){
                this.axios.get('http://127.0.0.1:3008/store/sort')
                .then(res=>{
                    this.sortList=res.data
                })
            },
            jump(id){
                if(id==1) this.$router.push('/goods')
            }
        },
        created(){
            this.getImg()
        }
    }
</script>
<style>

    div.app-sort .mui-bar{
        background:#FFB72F;
    }
    div.app-sort .mui-bar a{
        color:white;
    }
    div.app-sort .mui-bar a:nth-child(3){
        float:right;
        transform:rotate(90deg);
    }
    div.app-sort .mui-bar h1{
        color:white;
        letter-spacing:1px;
    }   
    div.app-sort .content {
        position:relative;
        top:2.9rem;
        left:0;
        margin-bottom:8rem;
    }
    div.app-sort>img{
        position:relative;
        top:2.9rem;
        width:100%;
    }
    div.app-sort>img:nth-child(3){
        margin-top:-0.6rem;
    }   
    div.app-sort .content .mui-table-view{
        background:white;
    }
    div.app-sort .mui-table-view li.mui-table-view-cell{
        width:33.33%;
    }
    div.app-sort .mui-table-view li.mui-table-view-cell a>div.mui-media-body{
        font-size:0.7em;
    }
    div.app-sort .mui-table-view li.mui-table-view-cell img{
        width:100%;
    }
    div.app-sort .mui-table-view li.mui-table-view-cell a>img[lazy=loading]{
        width:100%;
        background:#eee;
    }
</style>


