<template>
    <div class='app-info'>
        <header class="mui-bar mui-bar-nav">
		    <a @click='goBack()' class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <h1 class="mui-title">商品详情</h1>
            <a class='mui-icon mui-icon-more'></a>
		</header>
        <div v-if='isSee' class='load'>
            <i class="fa fa-spinner fa-pulse"></i><br><br>
            <span>加载中</span>
        </div>
        <div v-else class='content'>
            <div class='imgdetail'>
                <mt-swipe>
                    <mt-swipe-item>
                        <img src="../../img/d1.jpg">
                    </mt-swipe-item>
                    <mt-swipe-item>
                        <img src="../../img/d2.jpg">
                    </mt-swipe-item>
                    <mt-swipe-item>
                        <img src="../../img/d3.jpg">
                    </mt-swipe-item>
                    <mt-swipe-item>
                        <img src="../../img/d4.jpg">
                    </mt-swipe-item>
                </mt-swipe>
            </div>
            <div class='information'>
                <h2><span>￥</span>118</h2>
                <p><span>价格:</span>&nbsp;<del>￥168</del></p>
                <p>
                    <i class='fa fa-info-circle'></i>&nbsp;
                    <span>您有10.00元购物专享红包，订单满58.00元可用</span>
                </p>
                <h4>海蓝连衣裙  变幻莫测、种类最多、最受青睐的款式</h4>
                <ul>
                    <li>快递:0.00</li>
                    <li>月销量1040件</li>
                    <li>库存1506</li>
                </ul>
                <span>
                    <span>好评：</span>
                    <i v-for='i in 4' :key='i' class='fa fa-star'></i>
                    <i class='fa fa-star-half-empty'></i>
                    &nbsp;<span>9.5分</span>
                </span>
                <span>淘购抢</span>
            </div>
            <div class='promised'>
                <ul class="mui-table-view">
                    <li class="mui-table-view-cell">
                        <a class="mui-navigate-right">
                            <span>优惠</span>&nbsp;&nbsp;<span> 津贴</span>
                            &nbsp;&nbsp;领津贴每满400减50
                        </a>
                    </li>
                    <li class="mui-table-view-cell">
                        <a class="mui-navigate-right">
                            <span>促销</span>&nbsp;&nbsp;<span> 积分</span>
                            &nbsp;&nbsp;购买可得5积分
                        </a>
                    </li>
                    <li class="mui-table-view-cell">
                        <a class="mui-navigate-right">
                            <span>服务</span>
                            &nbsp;&nbsp;正品保证 · 极速退款 · 七天退换
                        </a>
                    </li>
                </ul>
            </div>
            <div class='sized'>
                <ul class="mui-table-view" @click='showParam()'>
                    <li class="mui-table-view-cell">
                        <a class="mui-navigate-right">
                            <span>参数</span>
                            &nbsp;&nbsp;品牌&nbsp;系列...
                        </a>
                    </li>
                </ul>
            </div>
            <div class='com-nav'>
                <a href='#' @click.prevent='chgCom(0)' :class="hasF?'red-active':''">好评(999+)</a>
                <a href='#' @click.prevent='chgCom(1)' :class="hasS?'red-active':''">中评(12)</a>
                <a href='#' @click.prevent='chgCom(2)' :class="hasT?'red-active':''">差评(0)</a>
            </div>
            <div v-if='hasF' class="mui-card" @touchstart='pullLoad($event)' v-for='item in comList' :key='item.id'>
				<div class="mui-card-header">
                    <img :src='item.c_url'>
                    <span v-text='item.c_name'></span>
                    <span><i class='fa fa-map-marker'></i> {{item.c_local}}</span>
                    <span >{{item.c_str}}：</span>
                    <span><i v-for='i in item.c_starnum' :key='i' class='fa fa-star'></i></span>
                </div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner" v-text='item.content'></div>
				</div>
				<div class="mui-card-footer">{{item.c_time|dateFilter}}</div>
			</div>
            <div v-if='hasS' class="mui-card">
				<div class="mui-card-header">
                    <img src='../../img/icon-4.png'>
                    <span>纯**天然</span>
                    <span><i class='fa fa-map-marker'></i> 山西省太原市</span>
                    <span>三星中评：</span>
                    <span><i v-for='i in 3' :key='i' class='fa fa-star'></i></span>
                </div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
                        商品质量还行，一分钱一分货。
					</div>
				</div>
				<div class="mui-card-footer">2018-08-18 09:20:16</div>
			</div>
            <div v-if='hasT' class="mui-card">
				<div class="mui-card-content">
					<div class="mui-card-content-inner" style='text-align:center;color:#666;height:5rem;'>
                        该店铺目前没有差评记录哦~
					</div>
				</div>
			</div>
            <div v-if='isLoad' class='loadrefresh'>
                <span class='mui-spinner'></span>&nbsp;
                <span>正在加载...</span>
            </div>
            <div v-if='isHave' class='loadrefresh'>
                <span>没有更多了~</span>
            </div>
            <div v-if='isParam' class='bg'></div>
        </div>
        <transition name='fader'>
            <div v-if='isParam' class='gparams'> 
                <div class='pimg'>
                    <img src='../../img/p1.jpg'> 
                </div>
                <span @click='hideParam()' class='mui-icon mui-icon-closeempty'></span>
                <p><span>￥</span>318.00</p>
                <p>库存:1506</p>
                <hr>
                <ul>
                    <li>尺码:</li>
                    <li>X</li>
                    <li>M</li>
                    <li>L</li>
                    <li>ML</li>
                </ul>
                <ul>
                    <li>颜色:</li>
                    <li>红</li>
                    <li>黄</li>
                    <li>蓝</li>
                    <li>白</li>
                </ul>
                <ul>
                    <li>面料:</li>
                    <li>纯棉</li>
                    <li>羊毛</li>
                </ul>
                <button @click='hideParam()' type="button" class="mui-btn mui-btn-danger">
                    确定
                </button>
            </div>
        </transition>  
    </div>
</template>
<script>
    export default {
        data(){
            return{
                isSee:true,
                isParam:false,
                isLoad:false,
                isHave:false,
                hasF:true,
                hasS:false,
                hasT:false,
                pno:0,
                pageSize:4,
                comList:[]
            }
        },
        methods:{
            goBack(){
                this.$router.push('/goods')
                this.$store.commit('chgShow',0)
            },
            //切换好评中评差评
            chgCom(i){
                switch (i){
                    case 0:
                        this.hasS=false;
                        this.hasT=false;
                        this.hasF=true;
                        break;
                    case 1:
                        this.hasF=false;
                        this.hasS=true;
                        this.hasT=false;
                        break; 
                    default:
                        this.hasF=false;
                        this.hasS=false;
                        this.hasT=true;
                }
            },
            //获取评论信息
            getComments(){
                this.pno++;
                if(this.pno>3){
                    this.isHave=true
                    return;
                } 
                this.axios.get(
                    'http://127.0.0.1:3008/store/comments',
                    {
                        params:{
                            pno:this.pno,
                            pageSize:this.pageSize
                        }
                    }
                )
                .then(res=>{
                    this.comList=this.comList.concat(res.data) 
                })
            },
            delay(){
                var t = setTimeout(()=>{
                    this.$store.commit('chgLoad',1)
                    this.isSee=false;
                },1500)
            },
            showParam(){
                this.$store.commit('chgShow',4)
                this.isParam=!this.isParam
            },
            hideParam(){
                this.isParam=false;
                this.$store.commit('chgShow',2)
            },
            //上拉加载
            pullLoad(e){
                var y = parseInt(e.changedTouches[0].pageY)
                var y2;
                var dc = document.documentElement.clientHeight
                var dst = document.documentElement.scrollTop
                var dsh = document.documentElement.scrollHeight
                document.ontouchmove=(e)=>{
                    y2 = parseInt(e.changedTouches[0].pageY)
                }
                document.ontouchend=()=>{           
                    if(y>y2 && (Math.ceil(dc+dst)>=dsh) && this.pno<=3){
                        this.isLoad=true;
                        setTimeout(()=>{
                            document.documentElement.scrollTop =parseInt(document.documentElement.scrollTop+100) 
                        },200) 
                        setTimeout(()=>{
                            this.isLoad=false;
                            this.getComments()
                        },1500)   
                    }
                    document.ontouchmove=null
                }
            }
        },
        created(){
            this.$store.commit('chgShow',2)
            this.getComments()
            if(this.$store.getters.hasLoad[1]==0) this.delay() 
            else this.isSee=false;
        }
    }
</script>
<style>
    .fader-enter-active,.fader-leave-active{transition:all 0.5s linear;}
    .fader-enter,.fader-leave-to{transform:translateY(300px);opacity:0;}
    .bg{
        position:fixed;
        top:0;
        width:100%;
        height:100rem;
        background:rgba(0,0,0,.5);
        z-index:1;
    }
    .loadrefresh{
        width:100%;
        text-align:center;
        padding:0.5rem 0;
        background:#ddd;
    }
    .loadrefresh>span:first-child{
        vertical-align:top;
    }
    .loadrefresh>span:last-child{
        font-size:0.6rem;
        color:#666;
        letter-spacing:1px;
    }
    .gparams{
        position:fixed;
        bottom:0rem;
        width:100%;
        height:21rem;
        background:white;
        box-shadow:-1px -1px 5px #bbb;
        z-index:2;
    }
    .pimg{
        position:absolute;
        width:7rem;
        height:7rem;
        border:3px solid #ddd;
        left:10%;
        top:-2.2rem;
        border-radius:0.8rem;
        text-align:center;
        background:white;
        padding-top:0.1rem;
    }
    .pimg>img{width:96%; border-radius:1rem;}
    .gparams>span{
        position:absolute;
        top:0.5rem;
        right:1rem;
        font-size:1.6rem;
        color:#666;
        border:1px solid #bbb;
        border-radius:50%;
    }
    .gparams>p:nth-child(3){
        position:absolute;
        top:1rem;
        left:11rem;
        color:red;
        font-size:1.5rem;
    }
    .gparams>p:nth-child(3)>span{font-size:1rem;}
    .gparams>p:nth-child(4){
        position:absolute;
        top:3rem;
        left:11rem;
        color:#333;
    }
    .gparams>hr:nth-child(5){
        position:absolute;
        top:5.5rem;
        width:98%;
        left:1%;
        height:1px;
        background:#eee;
        border:0;
    }
    .gparams>ul{
        position:absolute;
        list-style:none;
        width:80%;
        left:10%;
        top:7rem;
        margin:0;
        padding:0;
    }
    .gparams>ul:nth-child(7){top:9.5rem;}
    .gparams>ul:nth-child(8){top:12rem;}
    .gparams>ul:after{
        content:'';
        display:block;
        clear:both;
    }
    .gparams>ul>li{
        float:left;
        font-size:0.8rem;
        text-align:center;
        border-radius:0.2rem;
    }
    .gparams>ul:nth-child(6)>li:nth-child(3),.gparams>ul:nth-child(8)>li:nth-child(2){background:rgba(255,0,0,.9);color:white;}
    .gparams>ul:nth-child(7)>li:last-child{background:rgba(255,0,0,.9);color:white;}
    .gparams>ul>li:first-child{font-size:0.9rem;;width:20%;}
    .gparams>ul>li:not(:first-child){
        width:19%;
        border:1px solid #999;
        margin-right:1%;
    }
    div.gparams .mui-btn-danger{
        position:absolute !important;
        bottom:0;
        width:100%;
        height:3rem;
        background-color:red !important;
        letter-spacing:0.5rem;
    }
    [v-cloak]{display:none;}
    div.app-info .mui-bar{
        background:#FFB72F;
    }
    div.app-info .mui-bar a{
        color:white;
    }
    div.app-info .mui-bar>h1{
        color:white;
    }
    div.app-info .mui-bar a:nth-child(3){
        float:right;
        transform:rotate(90deg);
    }
    div.app-info .load{
        position:absolute;
        top:16rem;
        width:100%;
        height:10rem;
        text-align:center;
        padding-top:1rem;
    }
    div.app-info .load>i{
        font-size:3.2rem;
        color:#999;
        margin:0;
    }
    div.app-info .load>span{
        font-size:0.8rem;
        color:#666;
        letter-spacing:2px;
    }
    div.app-info .content{
        position:relative;
        top:2.9rem;
        width:100%;
        margin-bottom:6em;
    }
    div.app-info .content .imgdetail{
        width:100%;
        padding:0;
        margin:0;  
        background:white;
        height:15em;
    }
    div.app-info .content .imgdetail img{
        width:100%;
        height:100%;
        padding:0;
        margin:0;    
    }
    div.app-info .content .information{
        position:relative;
        top:0;
        left:0;
        height:8em;
        background:white;
    }
    div.app-info .content .information:after{
        content:'';
        display:block;
        clear:both;
    }
    div.app-info .content .information h2{
        position:absolute;
        top:0.1em;
        left:1%;
        font-size:1.5rem;
        color:red;
    }
    div.app-info .content .information h2>span{
        font-size:1.1rem;
    }
    div.app-info .content .information p:nth-child(2){
        position:absolute;
        top:2.4em;
        left:2%;
    }
    div.app-info .content .information p:nth-child(2)>span{
        font-size:0.85em;
    }
    div.app-info .content .information p:nth-child(3){
        position:absolute;
        top:5.1em;
        left:2%;
        font-size:0.7em;
        color:red;
    }
    div.app-info .content .information h4{
        position:absolute;
        top:6em;
        left:2%;
        font-size:0.8em;
        font-weight:normal;
    }
    div.app-info .content .information ul{
        position:absolute;
        width:96%;
        left:2%;
        bottom:0.2em;
        list-style:none;
        margin:0;
        padding:0;
    }
    div.app-info .content .information ul>li{
        float:left;
        width:33.333%;
        text-align:center;
        font-size:0.7rem;
        color:#666;
    }
    div.app-info .content .information ul>li:first-child{text-align:left;}
    div.app-info .content .information ul>li:last-child{text-align:right;}
    div.app-info .content .information>span:last-child{
        position:absolute;
        width:15%;
        left:21%;
        top:0.6em;
        text-align:center;
        padding-top:0.1em;
        background:#EE0000;
        font-size:0.8rem;
        color:white;
        border-radius:2px;
    }
    div.app-info .content .information>span:nth-child(6){
        position:absolute;
        top:0.6em;
        right:2%;
        color:#FFB72F;
        font-size:0.8rem;
    }
    div.app-info .content .information>span:nth-child(6)>span{
        font-size:0.7rem;
        color:#666;
    }
    div.app-info .content .promised{
        position:relative;
        top:0.5rem;
        width:100%;
    } 
    div.app-info .content .sized{
        position:relative;
        top:1rem;
        width:100%;
        background:white;
    }
    div.app-info .content .promised a,div.app-info .content .sized a{
        padding-left:2%;
        font-size:0.6rem;
    }
    div.app-info .content .promised>ul>li>a>span:first-child,div.app-info .content .sized>ul>li>a>span{
        font-size:0.6rem;
        color:#666;
    }
    div.app-info .content .promised>ul>li>a>span:nth-child(2){
        font-size:0.5rem;
        color:red;
        background:pink;
        text-align:center;
        padding:1px 3px 1px 0;
    }
    div.app-info .com-nav{
        position:relative;
        background:white;
        top:1.5rem;
        padding:0.5rem 1rem;
        margin-bottom:0.5rem;
    }
    div.app-info .com-nav:after{
        content:'';
        display:block;
        clear:both;
    }
    div.app-info .com-nav a{
        float:left;
        width:33.33%;
        text-align:center;
        font-size:0.85rem;
        color:#666;
    }
    .red-active{
        color:red !important;
        border-bottom:1.5px solid red;
    }
    div.app-info .mui-card{
        margin:0.2rem 0 0 0;
        width:100%;
    }
    div.app-info .mui-card:nth-child(6){
        margin:1.5rem 0 0 0;
    }
    div.app-info .mui-card .mui-card-content-inner{color:#333;letter-spacing:1px;font-size:0.8rem;}
    div.app-info .mui-card .mui-card-header>img{border-radius:50%;}
    div.app-info .mui-card .mui-card-header>span:not(:last-child){
        position:absolute;
        font-size:0.8rem;
        color:#666;
    }
    div.app-info .mui-card .mui-card-header>span:nth-child(2){
        top:0.5rem;
        left:3.5rem;
    }
    div.app-info .mui-card .mui-card-header>span:nth-child(3){
        top:1.6rem;
        left:3.5rem;
        font-size:0.7rem;
    }
    div.app-info .mui-card .mui-card-header>span:nth-child(3)>i{
        font-size:0.8rem;
        color:#FFB72F;
    }
    div.app-info .mui-card .mui-card-header>span:nth-child(4){
        top:1.1rem;
        right:4.5rem;
    }
    div.app-info .mui-card .mui-card-header>span:last-child{
        position:absolute;
        right:0.7rem;
        width:17%;
        text-align:left;
    }
    div.app-info .mui-card .mui-card-header>span:last-child>i{
        font-size:0.8rem;
        color:#FFB72F;
    }
    div.app-info .mui-card .mui-card-footer{
        font-size:0.8rem;
        color:#999;
        justify-content:flex-end;    
    }
</style>


