<template>
    <div class='app-find'>
        <header class="mui-bar mui-bar-nav">
		    <a @click='goBack(0)' class="mui-icon mui-icon-home"></a>
            <h1 class="mui-title">发现</h1>
            <a class='mui-icon mui-icon-more'></a>
		</header>
        <div class='content'>
            <mt-swipe>
                <mt-swipe-item>
                    <img src="../../img/ba1.jpg">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="../../img/ba2.jpg">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="../../img/ba3.jpg">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="../../img/ba4.jpg">
                </mt-swipe-item>
            </mt-swipe>
            <div class='time-over'>
                <p>距离结束还有:</p>
                <ul>
                    <li v-cloak>{{hours}}</li> 
                    <li> : </li>
                    <li v-cloak>{{minutes}}</li>
                    <li> : </li> 
                    <li v-cloak>{{seconds}}</li>
                </ul>
            </div>
            <div v-for='i in 3' :key='i' class="mui-card">
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
                        <img src='../../img/find1.jpg'>
                        <h4>新年送礼新花样，全新造型新气象！</h4>
                        <p>送礼才是新年正确的打开方式，可是<br>什么样的礼物才能深入人心并且在生活中有实质性的作用呢？香烟美酒特产美食？太过大众化。补品佳肴？价格高实际作用小。那到底什么样的新年礼物才算好呢？都说新年新气象，那不如送出能帮助别人打造“新气象”的礼物，用一套出色的理容设备，让亲朋好友们在新的一年里能有一个全新的姿态。</p>
                        <h6><span>￥</span>888.00</h6>
                        <button type="button" class="mui-btn mui-btn-danger">
                            立即抢购
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return{
                dTime:0,
                hours:0,
                minutes:0,
                seconds:0,
                timer:null
            }
        },
        methods:{
            goBack(i){
                this.$store.commit('change',i)
                this.$router.push('/home')
            },
            //抢购倒计时
            getTime(){
                var d = new Date('2018-11-16 12:00:00');
                var dt = d-new Date();
                this.dTime = dt ;
                if(this.dTime<=0){
                    clearInterval(this.timer);
                    return;
                } 
                this.hours = Math.floor(this.dTime/(1000*60*60))
                this.minutes = Math.floor((this.dTime/(1000*60*60)-this.hours)*60)
                this.seconds = Math.floor(((this.dTime/(1000*60*60)-this.hours)*60-this.minutes)*60)
                this.hours<10?this.hours='0'+this.hours:this.hours;
                this.minutes<10?this.minutes='0'+this.minutes:this.minutes;
                this.seconds<10?this.seconds='0'+this.seconds:this.seconds;
                this.dTime-=1000;
            }
        },
        mounted(){
            this.timer=setInterval(this.getTime,1000)
        }
    }
</script>
<style>
    [v-cloak]{display:none;}
    div.mint-swipe-indicator.is-active {
        background:#FFB72F;
        opacity:1;
    }
    div.mint-swipe-indicator{
        background:white;
        opacity:1;
    }
    .app-find .mint-swipe{
        height:10rem;
    }
    .app-find .mint-swipe img{
        width:100%;
        height:100%;
    }
    div.app-find .mui-bar{
        background:#FFB72F;
    }
    div.app-find .mui-bar a{
        color:white;
    }
    div.app-find .mui-bar a:nth-child(3){
        float:right;
        transform:rotate(90deg);
    }
    div.app-find .mui-bar h1{
        color:white;
        letter-spacing:1px;
    }
    div.app-find .content{
        position:relative;
        top:2.75rem;
        right:0;
        margin-bottom:8rem;
        overflow:hidden !important;
    }
    div.app-find .content .time-over{
        position:relative;
        top:0.2rem;
        width:100%;
        height:4.5rem;
        text-align:center;
        background:white;
        padding-top:0.5rem;
        margin-bottom:0;
    }
    div.app-find .content .time-over p:first-child{
        font-size:1.2rem;
        letter-spacing:1px;
        color:#C91923;
        font-weight:550;
        margin-left:0.5rem;
        margin-bottom:0;
    }
    div.app-find .content .time-over ul{
        list-style:none;
        width:100%;
        position:absolute;
        top:2.2rem;
        margin:0;
        padding:0;
        left:50%;
        margin-left:-3rem;
    }
    div.app-find .content .time-over ul>li{
        float:left;
        width:1.2rem;
        background:#F63844;
        text-align:center;
        color:white;
        font-size:0.8rem;
        border-radius:2px;
    }
    div.app-find .content .time-over ul>li:nth-child(2),div.app-find .content .time-over ul>li:nth-child(4){
        background:white;
        color:#F63844;
        font-weight:bold;
    }
    div.app-find .content .mui-card{
        width:100%;
        margin-left:0;
        margin-top:0;
    }
    div.app-find .content .mui-card img{
        position:relative;
        width:38%;
        height:100%;
        top:0;
        left:0;
    }
    div.app-find .content .mui-card h4{
        position:absolute;
        margin:0;
        width:50%;
        top:1rem;
        right:6%;
        font-size:0.75rem;
    }
    div.app-find .content .mui-card p{
        position:absolute;
        right:6%;
        top:2rem;
        width:50%;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
    }
    div.app-find .content .mui-card h6{
        position:absolute;
        right:6%;
        top:4.9rem;
        width:50%;
        font-size:1.2rem;
        color:red;
        font-weight:bold;
    }
    div.app-find .content .mui-card h6>span{
        font-size:0.8rem;
    }
    div.app-find .content .mui-card button{
        position:absolute;
        right:6%;
        top:4.7rem;
        font-size:0.8rem;
    }
    .mui-btn-danger{
        background-color:#EE2C2C !important;
    }
</style>


